import * as React from "react";
import { Map, Marker, Popup } from "react-map-gl";
import { Bridge } from "../../typing";
import "mapbox-gl/dist/mapbox-gl.css";
import { convertLatitude, convertLongitude } from "@/utils/geoConversion";
import { useState } from "react";

interface BridgeListProps {
  bridges: Bridge[];
}

const Mapbox: React.FC<BridgeListProps> = ({ bridges }) => {
  // Track the structureNumber of the clicked bridge
  const [selectedBridge, setSelectedBridge] = useState<string | null>(null);

  return (
    <div className="flex h-screen flex-grow w-full p-4 bg-white shadow-md rounded-md">
      <Map
        mapboxAccessToken={process.env.NEXT_PUBLIC_MAPBOX_TOKEN}
        initialViewState={{
          longitude: -79.99,
          latitude: 40.44,
          zoom: 10,
        }}
        mapStyle="mapbox://styles/ahmadhawamdah/cm4g9ldnm008m01ra7osc4t0w"
      >
        {bridges?.map((bridge) => (
          <div
            key={bridge.structureNumber}
            onClick={() => setSelectedBridge(bridge.structureNumber)}
          >
            <Marker
              longitude={convertLongitude(bridge.long)}
              latitude={convertLatitude(bridge.lat)}
              color="red"
              anchor="bottom"
            />
            {selectedBridge === bridge.structureNumber && (
              <Popup
                longitude={convertLongitude(bridge.long)}
                latitude={convertLatitude(bridge.lat)}
                anchor="top"
                onClose={() => setSelectedBridge(null)}
                closeOnClick={true}
              >
                <div className="z-20">
                  <p><strong>Structure:</strong> {bridge.structureNumber}</p>
                  <p><strong>Location:</strong> {bridge.location}</p>
                  <p><strong>Year Built:</strong> {bridge.yearBuilt}</p>
                </div>
              </Popup>
            )}
          </div>
        ))}
      </Map>
    </div>
  );
};

export default Mapbox;
